const getSingleList = (objList: any[]) => {
  const res: any[] = [];
  objList.forEach(item => {
    if (item.children) {
      item.children.forEach((child: any) => {
        if (child.fullscreen||child.notSinglePage) res.push(child.value);
      })
    }
  })
  return res;
}

export const singleList: any[] = getSingleList(allMethods)